@font-face {
  font-family: "SpecialFont";
  src: url(./Exo-VariableFont_wght.ttf);
  font-style: normal;
  font-weight: 100;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

img {
  max-width: 18vw;
  max-height: 18vw;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #303030;
  color: #ffffff;
}

.center-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  overflow: hidden;
}

.special-font {
  font-size: 2vmin;
  font-family: "SpecialFont";
}

.background-image {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url(background.png) no-repeat 50% fixed;
  background-size: cover;
}

.mint-descriptors-text {
  margin-left: auto;
  margin-right: auto;
  padding: 15px;
}

.mint-descriptors-div {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  background-color: rgba(0, 0, 0, 0.32);
  border-radius: 10px;
  margin: 2%;
  padding: 5%;
  flex-direction: row;
  flex-wrap: wrap;
}

.main-image {
  border-radius: 20px;
}

.mint-container {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 50%;
}
